@charset "UTF-8";

@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
  list-style-position: inside;
}

a {
  color: #007bff;
}

.buyBtn {
  background: #2262a5;
  color: #fff;
  padding: 5px;
  font-size: 15px;
  text-align: center;
  text-decoration: none;
  transition: 0.5s;
}

.buyBtn:hover {
  background: #3b7dc4;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 10px;
}

.section_title {
  color: #010101;
  width: 100%;
  text-align: center;
  font-size: 34px;
  border-bottom: 2px solid #010101;
  padding-bottom: 10px;
}

.hover {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
  transition: box-shadow .25s;
}

.hover:hover {
  box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.btn-up {
  position: fixed;
  color: white;
  right: 10px;
  bottom: 50px;
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  cursor: pointer;
  background: #2262a5;
  display: none;
  font-size: 30px;
  z-index: 3500;
  border: 1px solid #3075be;
  transition: .2s;
}

.btn-up:hover {
  border: 1px solid #3b7dc4;
  background: #3b7dc4;
  line-height: 40px;
}

.iframe_wrap {
  width: 100%;
  position: relative;
  height: 0;
  overflow: hidden;
  padding-bottom: 56.25%;
}

.iframe_wrap iframe,
.iframe_wrap object,
.iframe_wrap embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.nofixNav {
  position: static !important;
}

.nofixslider {
  margin-top: 0;
}

.modal {
  display: none;
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  z-index: 9999;
}

.modal .modal_item {
  background: #fff;
  padding: 10px;
  border-radius: 10px;
  max-width: 500px;
  margin: 10px auto;
}

.modal .modal_item .form_top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal .modal_item .form_top .close_modal {
  margin-bottom: 10px;
  cursor: pointer;
  text-align: end;
}

.modal .modal_item .form_top h3 {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  color: #2262a5;
}

.modal .modal_item .form_top h3 img {
  margin-right: 8px;
}

.modal .modal_item form {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
}

.modal .modal_item form input {
  width: 100%;
  padding: 8px 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 8px;
}

.modal .modal_item form button {
  width: 120px;
  margin-top: 5px;
  padding: 10px;
  background: green;
  color: #fff;
  font-size: 14px;
  border-radius: 5px;
  border: 1px solid green;
  transition: 0.5s;
}

.modal .modal_item form button:hover {
  background: #029602;
  border: 1px solid #029602;
  cursor: pointer;
}

.modal .modal_item form a {
  text-decoration: none;
}

.modal .modal_item form a:hover {
  text-decoration: underline;
}

.modal .modal_item form p {
  font-size: 10px;
  font-weight: bold;
  margin-bottom: 5px;
  color: #9a9a9a;
}

.modal .modal_item form p::before {
  content: "✓";
  color: green;
  margin-right: 3px;
}

.modal .modal_item .reg_info {
  margin-bottom: 13px;
  font-size: 12px;
  color: green;
  font-weight: bold;
}

.car_section {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  margin-top: 30px;
}

.car_section h1 {
  margin-bottom: 20px;
}

.car_section .car_section_item {
  width: 300px;
  margin-bottom: 30px;
}

.car_section .car_section_item .car_section_item_cover {
  width: 100%;
  max-height: 150px;
  overflow: hidden;
  position: relative;
}

.car_section .car_section_item .car_section_item_cover .follow {
  display: flex;
  justify-content: center;
  flex-flow: column;
  align-items: center;
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  z-index: 9;
  opacity: 0;
  transition: 0.5s;
}

.car_section .car_section_item .car_section_item_cover .follow p {
  color: #fff;
  font-size: 15px;
  text-align: center;
  margin-bottom: 15px;
}

.car_section .car_section_item .car_section_item_cover .follow a img {
  width: 32px;
  height: 32px;
}

.car_section .car_section_item .car_section_item_cover .follow a img:hover {
  transform: scale(1.3);
}

.car_section .car_section_item .car_section_item_cover img {
  width: 100%;
  transition: 0.5s;
  object-fit: cover;
}

.car_section .car_section_item h3 {
  margin-top: 5px;
  padding: 0 5px;
}

.car_section .car_section_item h3 a {
  text-decoration: none;
  color: #4b4b4b;
  font-family: 'Roboto', sans-serif;
}

.car_section .car_section_item p {
  padding: 0 5px;
  color: #707070;
}

.car_section .car_section_item .buyBtn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-top: 8px;
  padding: 10px;
  font-size: 15px;
}

.car_section .car_section_item .buyBtn img {
  margin-left: 10px;
  width: 18px;
  height: 18px;
}

.car_section .car_section_item:hover h3 a {
  color: #2262a5;
}

.car_section .car_section_item:hover .car_section_item_cover img {
  transform: scale(1.1);
}

.car_section .car_section_item:hover .car_section_item_cover .follow {
  opacity: 1;
}

.car_section .car_section_item:hover .buyBtn {
  background: #3b7dc4;
}

.seeMore {
  width: 100%;
  text-align: end;
}

.seeMore a {
  color: #fff;
  background: #2262a5;
  text-decoration: none;
  font-size: 14px;
  padding: 8px;
  border-radius: 5px;
  transition: .5s;
}

.seeMore a:hover {
  background: #3b7dc4;
}

.item_contact.phone .telephone,
.item_contact.phone .email,
.item_contact.mail .telephone,
.item_contact.mail .email {
  display: flex;
  align-items: center;
  font-size: 15px;
  font-weight: bold;
  margin-bottom: 10px;
}

.item_contact.phone .telephone a,
.item_contact.phone .email a,
.item_contact.mail .telephone a,
.item_contact.mail .email a {
  display: flex;
  align-items: center;
  text-decoration: none;
}

.item_contact.phone .telephone a img,
.item_contact.phone .email a img,
.item_contact.mail .telephone a img,
.item_contact.mail .email a img {
  width: 24px;
  height: 24px;
  margin-right: 8px;
}

.item_contact.phone .telephone span,
.item_contact.phone .email span,
.item_contact.mail .telephone span,
.item_contact.mail .email span {
  margin-left: 8px;
  font-size: 14px;
}

.price {
  margin-top: 5px !important;
  color: #ff0000 !important;
}

.filtr_searchBtn {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #2262a5;
  border: 1px solid #2262a5;
  color: #fff;
  transition: 0.5s;
  cursor: pointer;
  font-size: 16px;
  padding: 6px 10px !important;
  height: auto !important;
}

.filtr_searchBtn:hover {
  background: #3b7dc4;
