nav {
  background: #eeeeee;
  border-bottom: 2px solid #ccc;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 3334;
}

nav .nav {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
}

nav .nav a {
  text-decoration: none;
  color: #010101;
  font-weight: bold;
}

nav .nav a:hover {
  color: #007bff;
  border-bottom: 2px solid #007bff;
}

nav .nav .menu-btn {
  display: none;
  cursor: pointer;
  width: 40px;
  height: 40px;
  background: #2262a5;
  border-radius: 50%;
  position: relative;
  z-index: 3;
}

nav .nav .menu-btn .menu-btn span,
nav .nav .menu-btn .menu-btn span::before,
nav .nav .menu-btn .menu-btn span::after {
  position: absolute;
  top: 50%;
  margin-top: -1px;
  left: 50%;
  margin-left: -10px;
  width: 20px;
  height: 2px;
  background-color: #fff;
}

nav .nav .menu-btn .menu-btn span::before,
nav .nav .menu-btn .menu-btn span::after {
  content: '';
  display: block;
  transition: 0.2s;
}

nav .nav .menu-btn .menu-btn span::before {
  transform: translateY(-5px);
}

nav .nav .menu-btn .menu-btn_active span {
  height: 0;
}

nav .nav .menu-btn .menu-btn span::after {
  transform: translateY(5px);
}

nav .nav .menu-btn .menu-btn_active span:before {
  transform: rotate(-45deg);
  transform-origin: center;
}

nav .nav .menu-btn .menu-btn_active span:after {
  transform: rotate(45deg);
  transform-origin: center;
}

nav .nav .nav_menu {
  display: flex;
  justify-content: space-between;
  flex-flow: row wrap;
  width: 540px;
}

nav .nav .nav_menu li {
  list-style-type: none;
}

nav .nav .nav_menu li a {
  padding-bottom: 5px;
}

nav .nav .login_block a {
  padding: 5px 10px;
  margin: 0 5px;
}

nav .nav .login_block a.mobileReg,
nav .nav .login_block a.mobileLog {
  display: none;
}

nav .nav .login_block a.reg_open {
  background: #2262a5;
  color: #fff;
  transition: 0.5s;
  border-radius: 5px;
}

nav .nav .login_block a.reg_open:hover {
  background: #3b7dc4;
  border-bottom: none;
}

nav .nav .languages a {
  display: inline-block;
  width: 40px;
}

nav .nav .languages a:hover {
  border-bottom: none;
}

nav .nav .languages a img {
  width: 100%;
  border-radius: 5px;
}

