  align-items: center;
  justify-content: center;
  background: #2262a5;
  border: 1px solid #2262a5;
  color: #fff;
  transition: 0.5s;
  cursor: pointer;
  font-size: 16px;
  padding: 6px 10px !important;
  height: auto !important;
}

.filtr_searchBtn:hover {
  background: #3b7dc4;
  border: 1px solid #3b7dc4;
}

.filtr_searchBtn img {
  margin-left: 8px;
  width: 14px;
  height: 14px;
}

body {
  font-size: 16px;
  font-family: arnamu, arnamu_bold, 'Roboto', sans-serif;
  color: #212121;
  letter-spacing: 1px;
}

@media screen and (min-width: 1380px) {
  .slider .fade .slider_item .slider_content {
    position: absolute;
    background: rgba(0, 0, 0, 0.5);
    padding: 20px;
    left: 0;
    top: 0;
    max-width: 500px;
    height: 200px;
  }

  .slider .fade .slider_item .slider_content h3 {
    margin-bottom: 5px;
    font-size: 36px;
  }

  .slider .fade .slider_item .slider_content p {
    margin-bottom: 15px;
    font-size: 18px;
  }

  .slider .fade .slider_item .slider_content a {
    display: inline-block;
    background: #2262a5;
    padding: 10px;
    border-radius: 8px;
    font-size: 14px;
    text-decoration: none;
    color: #fff;
    transition: 0.5s;
  }

  .slider .fade .slider_item .slider_content a:hover {
    background: #3b7dc4;
  }
}

